import React, {Component} from 'react';

const logo = `${process.env.PUBLIC_URL}/images/logo.png`;

class TopBar extends Component {
    state = {pesquisa:''}
    render(){
        const { pesquisaBarShow, onClickPesquisar, onClickCategoria } = this.props;
        return (
            <div className="TopBar">
                <div className="flex horizontal">
                    <div className="flex-2 flex flex-center">
                        <img className="img-logo" src={logo} />
                    </div>
                    <div className="flex-5 flex horizontal-only flex-end itens">
                        <div className="flex horizontal-only flex-center clickable">
                            <i className="fa fa-user fa-lg" />
                            &nbsp;
                            <span>
                                Minha Conta
                            </span>
                        </div>
                        <div className="flex horizontal flex-center clickable">
                            <i className="fa fa-shopping-cart fa-lg" />
                        </div>
                        {
                            pesquisaBarShow &&
                            <div className="flex horizontal flex-center">
                                < input
                                    className="pesquisa-input clickable"
                                    name={"pesquisa"}
                                    placeholder="Pesquise aqui..."
                                    onChange={(ev)=>this.setState({ pesquisa: ev.target.value })} />
                            </div>
                        }
                        <div 
                            className="flex horizontal flex-center clickable"
                            onClick={onClickPesquisar} >
                            <i className="fa fa-search fa-lg" />
                        </div>
                        <div 
                            onClick={onClickCategoria} 
                            className="flex horizontal flex-center clickable">
                            <i className="fa fa-bars fa-lg" />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default TopBar;