import React, {Component} from 'react';

class Detalhes extends Component {

    state = {
        select: "descricao"
    }

    renderCabecalho(){
        return [
            (
                <div>
                    <small>
                        {"< Voltar"}
                    </small>
                </div>    
            ),
            (
                <div>
                    <h1 className="text-center">
                        Bolsa linhas tampa – bolso faca
                    </h1>
                </div>
            ),
            (
                <div>
                    <h2 className="text-center price">
                        <span>R$ </span>
                        829
                    </h2>
                </div>
            )
        ]
    }

    renderOpcoes(){
        return (
            <div className="details">
                <div>
                    <label>Ref:&nbsp;</label>
                    <span className="black-color">LP19V.A.61</span>
                </div>
                <div>
                    <label>Tamanho:&nbsp;</label>
                    <select className="black-color">
                        <option>Único</option>
                    </select>
                </div>
                <div>
                    <label>Quantidade:&nbsp;</label>
                    <input className="black-color" value="1" type="number" />
                </div>
            </div>
        )
    }

    renderDetails(select){
        return select === 'descricao' ? (
            <div className="black-color">
                <p>
                    Linhas coloridas e contrastantes na tampa guardam um segredo: um bolso faca que só você saberá que existe. 
                </p>
                <p>
                    Por dentro, forro de tecido e bolso interno.
                </p>
                <p>
                    Alça regulável.
                </p>
            </div>
        ) : (
            <div className="black-color">
                <p>
                    Altura: 24cm
                </p>
                <p>
                    Largura: 32cm
                </p>
                <p>
                    Alça: 1 metro e 27 cm no total
                </p>
            </div>
        )
    }

    renderDescricao(){
        const { select } = this.state
        return (
            <div className="painel">
                <div className="painel-cabecalho flex horizontal-only">
                    <div 
                        className={`padding-10 bottom-${select === 'descricao' ? "bold" : "thin"}`}
                        onClick={() => this.setState({ select: "descricao" })} >
                        Descrição
                    </div>
                    <div 
                        className={`padding-10 bottom-${select === 'dimensoes' ? "bold" : "thin"}`}
                        onClick={() => this.setState({ select: "dimensoes" })} >
                        Dimensões
                    </div>
                </div>
                <div className="painel-body">
                    { this.renderDetails(select)}
                </div>
            </div>
        )
    }

    renderComprar(){
        return (
            <div className="bottom-price flex-1 flex horizontal-only">
                <div className="flex-3 flex flex-center">
                    <h2 className="price">
                        <span>R$ </span>
                        829
                    </h2>
                </div>
                <div className="flex-2 flex flex-center">
                    <button className="btn-cta">
                        COMPRAR
                    </button>
                </div>
            </div>
        )
    }

    render(){
        return (
            <div className="Detalhes flex-1 flex vertical">                
                <div className="base flex-10 flex vertical">
                    { this.renderCabecalho() }
                    { this.renderOpcoes() }
                    <br />
                    { this.renderDescricao() }
                </div>
                <div className="flex-1 flex vertical">
                    { this.renderComprar() }
                </div>
            </div>
        )
    }
}

export default Detalhes;