import React, {Component} from 'react';

const pics = [
    `${process.env.PUBLIC_URL}/images/foto2.jpeg`,
    `${process.env.PUBLIC_URL}/images/foto1.jpeg`
];

class Fotos extends Component {
    state = {
        pic: pics[0]
    }

    render(){
        const { pic } = this.state;
        return (
            <div 
                className="Fotos flex-1 flex vertical flex-end-end padding-30"
                style={{
                    backgroundImage: `url(${pic})`,
                    backgroundSize: "cover",
                    backgroundPosition: "50% 80%"
                }} >
                <div
                    className="bolinha"
                    onClick={() => this.setState({ pic: pics[0] })} >
                </div>
                <div
                    className="bolinha"
                    onClick={() => this.setState({ pic: pics[1] })} >
                </div>
            </div>
        )
    }
}

export default Fotos;