import React, {Component} from 'react';

import TopBar from './TopBar';
import Categorias from './Categorias';

class NavBar extends Component {
    state = {
        categoriasShow: false,
        pesquisaBarShow: false
    }

    onClickCategoria=()=>{
        this.setState({ categoriasShow: !this.state.categoriasShow });
    }

    onClickPesquisar=()=>{
        this.setState({ pesquisaBarShow: !this.state.pesquisaBarShow });
    }

    render(){
        const { categoriasShow, pesquisaBarShow } = this.state;
        return (
            <div className="NavBar flex vertical">
                <TopBar 
                    onClickCategoria={this.onClickCategoria} 
                    onClickPesquisar={this.onClickPesquisar}
                    pesquisaBarShow={pesquisaBarShow} />
                { categoriasShow && <Categorias />}
            </div>
        )
    }
}

export default NavBar;